/* eslint-disable eol-last */
import Vuex from 'vuex'
import Vue from 'vue'
import createPersistedState from 'vuex-persistedstate'
import corporate from './corporate'
import employee from './corporate/employee'
import axios from 'axios'
import network from "../network/Base.js";

Vue.use(Vuex)
const http = network();
const token = localStorage.getItem('token')
if (token) {
    console.log(token)
    http.defaults.headers.common['Authorization'] = token
}
export default new Vuex.Store({
    strict: true,
    state: {
        baseApi: 'https://api.senseble.health:23000/',
        status: '',
        token: localStorage.getItem('token') || '',
        refreshToken: localStorage.getItem('refreshToken') || '',
        user: {},
        sz_companyname: '',
        sz_website: '',
        sz_phoneno: '',
        sz_address: '',
        hasBilling: false,
        hasPolicy: false,
        activeLink: 0
    },
    mutations: {
        auth_request(state) {
            state.status = 'loading'
        },
        has_policy(state, status) {
            state.hasPolicy = status
        },
        has_billing(state, status) {
            state.hasBilling = status
        },
        active_link(state, index) {
            state.activeLink = index
        },
        auth_success(state, {
            token,
            refreshToken,
            user
        }) {
            state.status = 'success'
            state.token = token
            state.user = user
            state.refreshToken = refreshToken
        },
        company_signup_details(state, {
            sz_companyname,
            sz_website,
            sz_phoneno,
            sz_address
        }) {
            state.sz_companyname = sz_companyname
            state.sz_website = sz_website
            state.sz_phoneno = sz_phoneno
            state.sz_address = sz_address
        },
        auth_error(state) {
            state.status = 'error'
        },
        logout(state) {
            state.status = ''
            state.token = ''
            state.refreshToken = ''
        },
    },

    actions: {
        login({
            commit
        }, {
            email,
            password
        }) {
            return new Promise((resolve, reject) => {
                commit('auth_request')
                http.post('auth/corporate/admin/signin', {
                        sz_email: email,
                        sz_password: password
                    })
                    .then(resp => {
                        const token = resp.data.token.access_token
                        const refreshToken = resp.data.token.refresh_token
                        const user = resp.data.user
                        localStorage.setItem('token', token)
                        localStorage.setItem('refreshToken', refreshToken)
                        http.defaults.headers.common['Authorization'] = token
                        commit('corporate/setLoggedIn', true)
                        commit('corporate/setCompanyDetails', user)
                        commit('auth_success', {
                            token,
                            refreshToken,
                            user
                        })
                        commit('corporate/setType', 'administrator')
                        resolve(resp)
                    })
                    .catch(err => {
                        commit('auth_error')
                        localStorage.removeItem('token')
                        reject(err)
                    })
            })
        },
        register({
            commit
        }, user) {
            return new Promise((resolve, reject) => {
                commit('auth_request')
                http.post('company/admin/register', user)
                    .then(resp => {
                        const token = resp.data.token
                        const user = resp.data.user
                        localStorage.setItem('token', token)
                        axios.defaults.headers.common['Authorization'] = token
                        commit('auth_success', token, user)
                        resolve(resp)
                    })
                    .catch(err => {
                        commit('auth_error', err)
                        localStorage.removeItem('token')
                        reject(err)
                    })
            })
        },
        fetchActivities({
            commit
        }, user) {
            return new Promise((resolve, reject) => {
                commit('auth_request')
                http.get('corporate/employee/activity', user)
                    .then(resp => {
                        resolve(resp)
                    })
                    .catch(err => {
                        commit('auth_error', err)
                        reject(err)
                    })
            })
        },
        fetchPeople({
            commit
        }, user) {
            return new Promise((resolve, reject) => {
                commit('auth_request')
                console.log(user)
                http.post('company/members/get', user)
                    .then(resp => {
                        resolve(resp)
                    })
                    .catch(err => {
                        commit('auth_error', err)
                        reject(err)
                    })
            })
        },
        addEmployeeAndDependents({
            commit
        }, user) {
            return new Promise((resolve, reject) => {
                commit('auth_request')
                console.log(user)
                http.post('corporate/members/add', user)
                    .then(resp => {
                        resolve(resp)
                    })
                    .catch(err => {
                        commit('auth_error', err)
                        reject(err)
                    })
            })
        },
        verifyCompanyExistence({
            commit
        }, {
            sz_companyname,
            sz_website,
            sz_phoneno,
            sz_address
        }) {
            return new Promise((resolve, reject) => {
                commit('auth_request')
                http.post('auth/corporate/verify', {
                        companyName: sz_companyname,
                        website: sz_website
                    })
                    .then(resp => {
                        // store company sign up details in state
                        commit('company_signup_details', {
                            sz_companyname,
                            sz_website,
                            sz_phoneno,
                            sz_address
                        });
                        resolve(resp)
                    })
                    .catch(err => {
                        commit('auth_error')
                        reject(err)
                    })
            })
        },
        logout({
            commit
        }) {
            return new Promise((resolve, reject) => {
                commit('logout')
                localStorage.removeItem('token')
                delete axios.defaults.headers.common['Authorization']
                resolve()
            })
        }
    },

    getters: {
        isLoggedIn: state => !!state.token,
        authStatus: state => state.status,
    },

    modules: {
        corporate,
        employee
    },

    plugins: [
        createPersistedState()
    ]
})