router.beforeEach((to, from, next) => {
    const isSignedIn = store.state.corporate.loggedIn
    const crole = store.state.corporate.type
    const cpageRole = to.meta.profile

    // exception for profile page
    if (to.fullPath.endsWith('/profile') && isSignedIn) {
        next()
    }

    if (to.meta.guest) {
        next()
    } else {
        if (to.meta.requireAuth) {
            const cauth = (cpageRole === crole)
            const cauthorized = (cauth && isSignedIn)
            if (cauthorized) {
                if (to.path === '/' && !store.state.corporate.company_id) {
                    next()
                } else
                    next()
            } else {
                next()
            }
        }
    }
})

export default router