<template>
    <div>
        <router-link
            :to="link.path"
            @click.native="linkClick"
            class="nav-link text-white justify-content-center"
            :target="link.target"
            :href="link.path">
        <div>
            <div class="text-center pb-2">
                <i :class="link.icon"></i>
            </div>
            <div class="nav-item mb-2">
                <template>
                    <span class="nav-link-text text-center">{{ link.name }}</span>
                </template>
            </div>
        </div>
        </router-link>
    </div>
</template>
<script>
  export default {
    name: 'sidebar-item',
    props: {
      link: {
        type: Object,
        default: () => {
          return {
            name: '',
            path: '',
            children: []
          };
        },
        description:
          'Sidebar link. Can contain name, path, icon and other attributes. See examples for more info'
      }
    },
    data() {
      return {
        children: [],
        collapsed: true
      };
    },
    methods: {
      linkClick() {
        if (
          this.autoClose &&
          this.$sidebar &&
          this.$sidebar.showSidebar === true
        ) {
          this.$sidebar.displaySidebar(false);
        }
      }
    }
  };
</script>
